import React, { useLayoutEffect, useRef } from 'react';
import { Form } from '../../../shared/useForm';
import Button from '@material-ui/core/Button';
import RadioGroup from '../../../components/Controls/RadioGroup';
import FormHelperText from '@material-ui/core/FormHelperText';
import classes from './EvaluationForm.module.css';

const PartakerForm = (props) => {
  const divRef = useRef(null);

  useLayoutEffect(() => {
    divRef.current.scrollIntoView({ block: 'end', behavior: 'instant' });
  }, []);

  const {
    values,
    evaluationItems,
    errors,
    setErrors,
    handleInputChange,
  } = props;

  const {
    question11D,
    question11I,
    question12D,
    question12I,
    question13aD,
    question13aI,
    question13bD,
    question13bI,
    question13cD,
    question13cI,
  } = values;

  const validate = () => {
    let temp = { ...errors };

    if (
      question11D &&
      question11I &&
      question12D &&
      question12I &&
      question13aD &&
      question13aI &&
      question13bD &&
      question13bI &&
      question13cD &&
      question13cI
    ) {
      temp.formError = '';
    } else {
      temp.formError = 'Asegúrese de contestar todas las preguntas';
    }

    setErrors({
      ...temp,
    });

    if (values) return Object.values(temp).every((x) => x == '');
  };

  const next = (e) => {
    e.preventDefault();
    if (validate()) {
      props.nextStep();
    }
  };

  const prev = (e) => {
    e.preventDefault();
    props.prevStep();
  };

  let form = (
    <Form className={classes.form}>
      <h4 ref={divRef}>PARTICIPANTE</h4>
      <div>
        <p>
          11. Los contenidos del curso son útiles para su desempeño profesional
          y laboral.
        </p>
        <RadioGroup
          name='question11D'
          label='Desempeño'
          value={question11D}
          onChange={handleInputChange}
          items={evaluationItems}
        />
        <RadioGroup
          name='question11I'
          label='Importancia'
          value={question11I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <div>
        <p>
          12. El curso lo ayudó a considerar nuevas formas de trabajo con sus
          compañeros.
        </p>
        <RadioGroup
          name='question12D'
          label='Desempeño'
          value={question12D}
          onChange={handleInputChange}
          items={evaluationItems}
        />
        <RadioGroup
          name='question12I'
          label='Importancia'
          value={question12I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>

      <div>
        <p>
          <strong>13. El curso que tomó</strong>
        </p>
        <p>13a. Le produjo un incremento en su motivación.</p>
        <RadioGroup
          name='question13aD'
          label='Desempeño'
          value={question13aD}
          onChange={handleInputChange}
          items={evaluationItems}
        />
        <RadioGroup
          name='question13aI'
          label='Importancia'
          value={question13aI}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <div>
        <p>13b. Le ha servido para su desarrollo personal.</p>
        <RadioGroup
          name='question13bD'
          label='Desempeño'
          value={question13bD}
          onChange={handleInputChange}
          items={evaluationItems}
        />
        <RadioGroup
          name='question13bI'
          label='Importancia'
          value={question13bI}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>

      <div>
        <p>13c. Le permitió desarrollar algunas habilidades adicionales.</p>
        <RadioGroup
          name='question13cD'
          label='Desempeño'
          value={question13cD}
          onChange={handleInputChange}
          items={evaluationItems}
        />

        <RadioGroup
          name='question13cI'
          label='Importancia'
          value={question13cI}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <FormHelperText error>{errors.formError}</FormHelperText>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={prev}
      >
        Regresar
      </Button>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={next}
      >
        Siguiente
      </Button>
    </Form>
  );

  return <div>{form}</div>;
};

export default PartakerForm;
