import { React, Fragment, useState, useLayoutEffect } from 'react';
import CourseEvaluationForm from './CourseEvaluationForm/CourseEvaluationForm';
import UnsubscribeToCourse from './UnsubscribeToCourse/UnsubscribeToCourse';
import MaterialTable from 'material-table';
import Popup from '../../components/UI/Popup/Popup';
import Spinner from '../../components/UI/Spinner/Spinner';
import Icon from '@material-ui/core/Icon';
import axios from 'axios';

import { Menu, MenuItem } from '@material-ui/core';

const styles = {
  maxWidth: '90%',
  margin: '1em auto',
};

const MyCourses = (props) => {
  const [anchorEl, setAnchorEl] = useState(false);
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [group, setGroup] = useState([]);
  const [openEvaluation, setOpenEvaluation] = useState(false);
  const [openUnsubscribe, setOpenUnsubscribe] = useState(false);
  const [auxUnsubscribed, setAuxUnsubscribed] = useState(false);
  const [moreInfo, setMoreInfo] = useState(true);

  //Se realiza la peticion cada vez que UnsubscribeToCourse se abre o cierra
  //para obtener los datos actualizados despues de desinscribirse
  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, [openUnsubscribe]);

  const request = () => {
    setLoading((loading) => !loading);
    //let url ='/mycourses'
    axios.get('/mycourses').then((response) => {
      setData(response.data);
      setLoading((loading) => !loading);
    });
  };
  //     .catch((error) => {
  //       setLoading((loading) => !loading);
  //       if (error.response) {
  //         //Se realizo la peticion y el servidor respondio con un codigo de status
  //         window.alert('Error: ' + error.response.data.message);
  //       } else if (error.request) {
  //         //Se realizo la peticion pero no se recibio una respuesta
  //         window.alert('Error: ' + error.request.data.message);
  //       } else {
  //         //Algo ocurrio al preparar la peticion que desencadeno un error
  //         console.log('Error: ' + error.message);
  //       }
  //       console.log(error.config);
  //     });
  // };

  const downloadEnrollmentForm = () => {
    //Se realizan dos consultas. La primera para obtener el nombre
    //del archivo y la segunda para obtener la cedula de inscripción
    const firstUrl = '/auth/mycourse-filename/';
    const secondUrl = '/auth/mycourse-download/';

    const requestOne = axios.get(firstUrl + group);
    const requestTwo = axios({
      url: secondUrl + group,
      method: 'GET',
      responseType: 'blob',
    });

    axios
      .all([requestOne, requestTwo])
      .then(
        axios.spread((...responses) => {
          //Se guardan las respuestas
          const fileName = responses[0];
          const fileDownload = responses[1];
          //Se realiza la descarga de la cedula de inscripción
          const downloadUrl = window.URL.createObjectURL(
            new Blob([fileDownload.data])
          );
          const link = document.createElement('a');
          link.href = downloadUrl;
          link.setAttribute('download', fileName.data.fileName); //any other extension
          document.body.appendChild(link);
          link.click();
          link.remove();
        })
      )
      .catch((errors) => {
        // react
      });
  };

  const openMenu = (event, row) => {
    setAnchorEl(event.currentTarget);
  };

  const closeMenu = (event, row) => {
    setAnchorEl(null);
  };

  const attendanceRedirect = (group) => {
    props.history.push('/attendance-partaker/' + group);
  };

  let courseEnrollmentTable = (
    <Fragment>
      <Popup
        title='Encuesta para participantes inscritos'
        openPopup={openEvaluation}
        setOpenPopup={setOpenEvaluation}
      >
        <CourseEvaluationForm rowData={group} closePopup={setOpenEvaluation} />
      </Popup>

      <Popup
        title='Baja de curso'
        openPopup={openUnsubscribe}
        setOpenPopup={setOpenUnsubscribe}
      >
        <UnsubscribeToCourse
          rowData={group}
          auxUnsubscribed={auxUnsubscribed}
          setAuxUnsubscribed={setAuxUnsubscribed}
          openUnsubscribe={openUnsubscribe}
          setOpenUnsubscribe={setOpenUnsubscribe}
        />
      </Popup>

      <MaterialTable
        title='Mis cursos (Asistencia/Descargar cedula/Evaluar curso)'
        columns={[
          { title: 'Curso', field: 'course' },
          { title: 'Periodo (dd/mm/aaaa)', field: 'period' },
          { title: 'Instructor - Email', field: 'instructor' },
          { title: 'Aula', field: 'classroom', sorting: false },
          { title: 'Estatus', field: 'status', sorting: false },
          { title: 'Departamento', field: 'department', hidden: moreInfo },
          {
            title: 'Tipo de capacitacion',
            field: 'trainingType',
            hidden: moreInfo,
          },
          { title: 'Grupo', field: 'group', hidden: true },
        ]}
        data={data}
        actions={[
          {
            icon: 'add_box',
            isFreeAction: true,
            iconProps: {
              style: { fontSizeLarge: '30px', colorAction: 'green' },
            },
            tooltip: 'Mostrar mas información',
            onClick: () => {
              setMoreInfo((moreInfo) => !moreInfo);
            },
          },
          {
            icon: 'more_horiz',
            tooltip: 'Acciones',
            isFreeAction: false,
            onClick: (event, rowData) => {
              openMenu(event);
              setGroup(rowData.group);
            },
          },
        ]}
        options={{
          pageSize: 5,
          pageSizeOptions: [5, 10, 15, 20],
          headerStyle: {
            backgroundColor: '#e4a010',
            color: '#FFF',
            fontSize: '15px',
            fontWeight: 'bold',
          },
          filterCellStyle: {
            backgroundColor: '#01579b',
            color: '#FFF',
          },
          searchFieldStyle: {
            backgroundColor: '#E8E8E8',
            color: 'black',
          },
          cellStyle: {},
        }}
        localization={{
          header: {
            actions: 'MENU DE ACCIONES     ',
          },
          toolbar: {
            searchTooltip: 'Buscar',
            searchPlaceholder: 'Buscar',
          },
          pagination: {
            labelDisplayedRows: '{from}-{to} de {count}',
            labelRowsSelect: 'Filas',
            firstAriaLabel: 'Primera pagina',
            firstTooltip: 'Primera pagina',
            previousAriaLabel: 'Pagina anterior',
            previousTooltip: 'Pagina anterior',
            nextAriaLabel: 'Siguiente pagina',
            nextTooltip: 'Siguiente pagina',
            lastAriaLabel: 'Ultima pagina',
            lastTooltip: 'Ultima pagina',
          },
          body: {
            emptyDataSourceMessage: 'No se ha inscrito a un curso',
          },
        }}
      />
      <Menu
        id='simple-menu'
        keepMounted
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={closeMenu}
      >
        <MenuItem onClick={() => attendanceRedirect(group)}>
          <Icon fontSize='small'>pan_tool</Icon> Asistencia
        </MenuItem>

        <MenuItem
          onClick={() => {
            setOpenEvaluation((openEvaluation) => !openEvaluation);
            closeMenu();
          }}
        >
          <Icon fontSize='small'>feedback</Icon> Evaluar Curso
        </MenuItem>

        <MenuItem
          onClick={() => {
            downloadEnrollmentForm(group);
            closeMenu();
          }}
        >
          <Icon fontSize='small'>vertical_align_bottom</Icon> Descargar cedula
        </MenuItem>

        <MenuItem
          style={{ backgroundColor: '#ff6666' }}
          onClick={() => {
            setOpenUnsubscribe((openUnsubscribe) => !openUnsubscribe);
            closeMenu();
          }}
        >
          <Icon fontSize='small'>delete_forever</Icon> Dar de baja
        </MenuItem>
      </Menu>
    </Fragment>
  );

  if (loading) {
    courseEnrollmentTable = <Spinner />;
  }

  return <div style={styles}>{courseEnrollmentTable}</div>;
};

export default MyCourses;
