import React from 'react';
import Grid from '@material-ui/core/Grid';
import { Form } from '../../../shared/useForm';

import Button from '@material-ui/core/Button';
import { List, ListItem, ListItemText } from '@material-ui/core/';
import Spinner from '../../../components/UI/Spinner/Spinner';

const CourseInfoForm = (props) => {
  const { courseInfo, loading } = props;
  const {
    course,
    responsibleUnit,
    instructor,
    campus,
    courseDate,
    hours,
    courseSchedule,
  } = courseInfo;

  const next = (e) => {
    e.preventDefault();
    props.nextStep();
  };

  let form = (
    <Form>
      <h4>Datos del curso</h4>
      <Grid container>
        <Grid item xs={6}>
          <List>
            <ListItem>
              <ListItemText primary='Curso' secondary={course} />
            </ListItem>
            <ListItem>
              <ListItemText
                primary='Unidad responsable'
                secondary={responsibleUnit}
              />
            </ListItem>
            <ListItem>
              <ListItemText primary='Instructor' secondary={instructor} />
            </ListItem>
            <ListItem>
              <ListItemText primary='Sede' secondary={campus} />
            </ListItem>
          </List>
        </Grid>
        <Grid item xs={6}>
          <List>
            <ListItem>
              <ListItemText
                primary='Fecha de realización'
                secondary={courseDate}
              />
            </ListItem>
            <ListItem>
              <ListItemText primary='Duración' secondary={hours} />
            </ListItem>
            <ListItem>
              <ListItemText primary='Horario' secondary={courseSchedule} />
            </ListItem>
          </List>
        </Grid>
      </Grid>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={next}
      >
        Siguiente
      </Button>
    </Form>
  );

  if (loading) {
    form = <Spinner />;
  }

  return <div>{form}</div>;
};

export default CourseInfoForm;
