import React, { useLayoutEffect, useRef } from 'react';
import { Form } from '../../../shared/useForm';
import Input from '../../../components/Controls/Input';
import Button from '@material-ui/core/Button';
import RadioGroup from '../../../components/Controls/RadioGroup';
import Checkbox from '@material-ui/core/Checkbox';
import FormHelperText from '@material-ui/core/FormHelperText';
import classes from './EvaluationForm.module.css';

const ServiceForm = (props) => {
  const divRef = useRef(null);

  useLayoutEffect(() => {
    divRef.current.scrollIntoView({ block: 'end', behavior: 'instant' });
  }, []);

  const {
    values,
    evaluationItems,
    errors,
    setErrors,
    handleInputChange,
    handleSubmit,

    question20a,
    question20b,
    question20c,
    setQuestion20a,
    setQuestion20b,
    setQuestion20c,
    toggleQuestion20Input,
  } = props;

  const {
    question14D,
    question14I,
    question15D,
    question15I,
    question16D,
    question16I,
    question17D,
    question17I,
    question18D,
    question18I,
    question19D,
    question19I,
    question20Input,
    commentaries,
  } = values;

  const validate = () => {
    let temp = { ...errors };

    if (
      question14D &&
      question14I &&
      question15D &&
      question15I &&
      question16D &&
      question16I &&
      question17D &&
      question17I &&
      question18D &&
      question18I &&
      question19D &&
      question19I
    ) {
      temp.formError = '';
    } else {
      temp.formError = 'Asegúrese de contestar todas las preguntas';
    }

    setErrors({
      ...temp,
    });

    if (values) return Object.values(temp).every((x) => x == '');
  };

  const prev = (e) => {
    e.preventDefault();
    props.prevStep();
  };

  const submit = (e) => {
    e.preventDefault();
    if (validate()) {
      handleSubmit(e);
    }
  };

  let form = (
    <Form className={classes.form} onSubmit={submit}>
      <h4 ref={divRef}>SERVICIO</h4>
      <div>
        <p>
          14. La iluminación del área fue adecuada para el desarrollo del curso.
        </p>
        <RadioGroup
          name='question14D'
          label='Desempeño'
          value={question14D}
          onChange={handleInputChange}
          items={evaluationItems}
        />
        <RadioGroup
          name='question14I'
          label='Importancia'
          value={question14I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <div>
        <p>
          15. La ventilación del aula fue adecuada para el desarrollo del curso.
        </p>
        <RadioGroup
          name='question15D'
          label='Desempeño'
          value={question15D}
          onChange={handleInputChange}
          items={evaluationItems}
        />
        <RadioGroup
          name='question15I'
          label='Importancia'
          value={question15I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>

      <div>
        <p>16. El aula estuvo limpia durante el desarrollo del curso.</p>
        <RadioGroup
          name='question16D'
          label='Desempeño'
          value={question16D}
          onChange={handleInputChange}
          items={evaluationItems}
        />
        <RadioGroup
          name='question16I'
          label='Importancia'
          value={question16I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <div>
        <p>17. El personal organizador del curso fue cortés.</p>
        <RadioGroup
          name='question17D'
          label='Desempeño'
          value={question17D}
          onChange={handleInputChange}
          items={evaluationItems}
        />
        <RadioGroup
          name='question17I'
          label='Importancia'
          value={question17I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <div>
        <p>18. El servicio de café fue adecuado. </p>
        <RadioGroup
          name='question18D'
          label='Desempeño'
          value={question18D}
          onChange={handleInputChange}
          items={evaluationItems}
        />
        <RadioGroup
          name='question18I'
          label='Importancia'
          value={question18I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <div>
        <p>19. Las condiciones de los sanitarios fueron adecuadas. </p>
        <RadioGroup
          name='question19D'
          label='Desempeño'
          value={question19D}
          onChange={handleInputChange}
          items={evaluationItems}
        />
        <RadioGroup
          name='question19I'
          label='Importancia'
          value={question19I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <div>
        <p>
          <strong>
            20. En caso de considerar que existan obstáculos que le impidan
            aplicar los conocimientos del curso, seleccione el correspondiente
          </strong>
        </p>
        <p>20a. Falta de equipo o de material</p>
        <Checkbox
          type='checkbox'
          checked={question20a || false}
          onChange={() => setQuestion20a((question20a) => !question20a)}
        />
        <p>20b. Falta de apoyo en el area de trabajo</p>
        <Checkbox
          type='checkbox'
          checked={question20b || false}
          onChange={() => setQuestion20b((question20b) => !question20b)}
        />
        <div>
          <p>20c. Otro.</p>
          <Checkbox
            type='checkbox'
            checked={question20c || false}
            onChange={() => {
              setQuestion20c((question20c) => !question20c);
              toggleQuestion20Input();
            }}
          />
          <Input
            disabled={!question20c}
            name='question20Input'
            placeholder='Explique'
            label='Explique'
            value={question20Input}
            onChange={handleInputChange}
            inputProps={{ maxLength: 100 }}
          />
        </div>
        <p>Comentarios</p>
        <Input
          name='commentaries'
          placeholder='Comentarios'
          label='Comentarios'
          value={commentaries}
          onChange={handleInputChange}
          inputProps={{ maxLength: 200 }}
        />
      </div>
      <FormHelperText error>{errors.formError}</FormHelperText>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={prev}
      >
        Regresar
      </Button>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
      >
        Finalizar encuesta
      </Button>
    </Form>
  );

  return <div>{form}</div>;
};

export default ServiceForm;
