import React, { useLayoutEffect, useRef } from 'react';
import { Form } from '../../../shared/useForm';
import Button from '@material-ui/core/Button';
import RadioGroup from '../../../components/Controls/RadioGroup';
import FormHelperText from '@material-ui/core/FormHelperText';
import classes from './EvaluationForm.module.css';

const InstructorForm = (props) => {
  const divRef = useRef(null);

  useLayoutEffect(() => {
    divRef.current.scrollIntoView({ block: 'end', behavior: 'instant' });
  }, []);

  const {
    values,
    evaluationItems,
    errors,
    setErrors,
    handleInputChange,
  } = props;

  const {
    question7D,
    question7I,
    question8D,
    question8I,
    question9D,
    question9I,
    question10D,
    question10I,
  } = values;

  const validate = () => {
    let temp = { ...errors };
    if (
      question7D &&
      question7I &&
      question8D &&
      question8I &&
      question9D &&
      question9I &&
      question10D &&
      question10I
    ) {
      temp.formError = '';
    } else {
      temp.formError = 'Asegúrese de contestar todas las preguntas';
    }
    setErrors({
      ...temp,
    });

    if (values) return Object.values(temp).every((x) => x == '');
  };

  const next = (e) => {
    e.preventDefault();
    if (validate()) {
      props.nextStep();
    }
  };
  const prev = (e) => {
    e.preventDefault();
    props.prevStep();
  };

  let form = (
    <Form className={classes.form}>
      <h4 ref={divRef}>INSTRUCTOR</h4>
      <div>
        <p>7. El instructor proyectó una imagen de confianza y credibilidad</p>
        <RadioGroup
          name='question7D'
          label='Desempeño'
          value={question7D}
          onChange={handleInputChange}
          items={evaluationItems}
        />

        <RadioGroup
          name='question7I'
          label='Importancia'
          value={question7I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <div>
        <p>8. Despertó y mantuvo el interés del participante.</p>
        <RadioGroup
          name='question8D'
          label='Desempeño'
          value={question8D}
          onChange={handleInputChange}
          items={evaluationItems}
        />

        <RadioGroup
          name='question8I'
          label='Importancia'
          value={question8I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>

      <div>
        <p>
          9. Expuso de manera clara y precisa el objetivo y el temario del
          curso.
        </p>
        <RadioGroup
          name='question9D'
          label='Desempeño'
          value={question9D}
          onChange={handleInputChange}
          items={evaluationItems}
        />

        <RadioGroup
          name='question9I'
          label='Importancia'
          value={question9I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <div>
        <p>10. Aclaró las dudas que se presentaron durante el curso.</p>
        <RadioGroup
          name='question10D'
          label='Desempeño'
          value={question10D}
          onChange={handleInputChange}
          items={evaluationItems}
        />

        <RadioGroup
          name='question10I'
          label='Importancia'
          value={question10I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <FormHelperText error>{errors.formError}</FormHelperText>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={prev}
      >
        Regresar
      </Button>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={next}
      >
        Siguiente
      </Button>
    </Form>
  );

  return <div>{form}</div>;
};

export default InstructorForm;
