import React, { useState, useLayoutEffect } from 'react';
import CourseInfoForm from './CourseInfoForm';
import EvaluationInfoForm from './EvaluationInfoForm';
import EventForm from './EventForm';
import InstructorForm from './InstructorForm';
import PartakerForm from './PartakerForm';
import ServiceForm from './ServiceForm';
import Spinner from '../../../components/UI/Spinner/Spinner';
import { Alert, AlertTitle } from '@material-ui/lab';
import axios from 'axios';

const evaluationItems = [
  { id: '1', title: '1' },
  { id: '2', title: '2' },
  { id: '3', title: '3' },
  { id: '4', title: '4' },
  { id: '5', title: '5' },
];

export const CourseEvaluationForm = (props) => {
  const [step, setStep] = useState(1);
  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [evaluationSend, setEvaluationSend] = useState(false);
  const [courseInfo, setCourseInfo] = useState({
    course: '',
    responsibleUnit: '',
    instructor: '',
    campus: '',
    courseDate: '',
    hours: '',
    courseSchedule: '',
  });
  //Cada pregunta de la encuesta contiene una evalación para desempeño y otra para
  //importancia. El estado para cada pregunta se nombró de la siguiente manera:
  // question#D, question#I
  //donde # corresponde al numero de pregunta, D a la evaluación de desempeño,
  // e I a la evaluación de Importancia.
  //Pregunta 13 y 20 cuenta con 3 apartados (a, b y c).
  const [values, setValues] = useState({
    question1D: '',
    question1I: '',
    question2D: '',
    question2I: '',
    question3D: '',
    question3I: '',
    question4D: '',
    question4I: '',
    question5D: '',
    question5I: '',
    question6D: '',
    question6I: '',
    question7D: '',
    question7I: '',
    question8D: '',
    question8I: '',
    question9D: '',
    question9I: '',
    question10D: '',
    question10I: '',
    question11D: '',
    question11I: '',
    question12D: '',
    question12I: '',
    question13aD: '',
    question13aI: '',
    question13bD: '',
    question13bI: '',
    question13cD: '',
    question13cI: '',
    question14D: '',
    question14I: '',
    question15D: '',
    question15I: '',
    question16D: '',
    question16I: '',
    question17D: '',
    question17I: '',
    question18D: '',
    question18I: '',
    question19D: '',
    question19I: '',
    question20Input: '',
    commentaries: '',
  });
  //CourseEvaluationForm obtiene estos datos desde props directamente, no de values
  //Se utilizan para los checkbox de la pregunta 20
  const [question20a, setQuestion20a] = useState(false);
  const [question20b, setQuestion20b] = useState(false);
  const [question20c, setQuestion20c] = useState(false);

  const { rowData, closePopup } = props;

  useLayoutEffect(() => {
    request();
    // eslint-disable-next-line
  }, []);

  const request = () => {
    setLoading((loading) => !loading);

    //let url ='/course-evaluation-form'
    axios
      .get('/course-evaluation-form/' + rowData)
      .then((response) => {
        setCourseInfo({
          course: response.data.course,
          responsibleUnit: response.data.responsibleUnit,
          instructor: response.data.instructor,
          campus: response.data.campus,
          //Periodo inicial a final
          courseDate: response.data.courseDate,
          //Total de horas del curso
          hours: response.data.hours,
          //Horas del dia
          courseSchedule: response.data.courseSchedule,
          group: rowData,
        });
        setLoading((loading) => !loading);
      })
      .catch((error) => {
        closePopup(false);
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          window.alert('Error: ' + error.response.data.message);
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
      });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setLoading((loading) => !loading);
    const arrValues = [];
    arrValues.push(courseInfo);
    arrValues.push(values);
    arrValues.push({ question20a });
    arrValues.push({ question20b });
    arrValues.push({ question20c });

    let objectValues = Object.assign({}, ...arrValues);

    //let url = '/auth/course-evaluation-form/' + rowData
    axios
      .post(
        'https://control-capacitacion-c6b9d.firebaseio.com/register-courses.json',
        objectValues
      )
      .then((response) => {
        setLoading((loading) => !loading);
        setEvaluationSend(true);
        setTimeout(() => closePopup(false), 3000);
      })
      .catch((error) => {
        if (error.response) {
          //Se realizo la peticion y el servidor respondio con un codigo de status
          window.alert('Error: ' + error.response.data.message);
        } else if (error.request) {
          //Se realizo la peticion pero no se recibio una respuesta
          window.alert('Error: ' + error.request.data.message);
        } else {
          //Algo ocurrio al preparar la peticion que desencadeno un error
          console.log('Error: ' + error.message);
        }
        console.log(error.config);
        closePopup(false);
      });
  };

  const nextStep = () => {
    setStep(step + 1);
  };

  const prevStep = () => {
    setStep(step - 1);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setValues({
      ...values,
      [name]: value,
    });
  };

  //Limpia el input de service form cuando se deja de seleccionar
  //Se llama en onChange del checkbox de la pregunta 20c
  const toggleQuestion20Input = () => {
    setValues({ ...values, question20Input: '' });
  };

  //Utiliza referencia divRef para hacer scroll al principio despues
  //de ir a la siguiente pagina o regresarse a una pagina.

  let courseInfoForm = (
    <CourseInfoForm
      nextStep={nextStep}
      values={values}
      courseInfo={courseInfo}
      handleInputChange={handleInputChange}
    />
  );

  let serviceForm = (
    <ServiceForm
      prevStep={prevStep}
      handleSubmit={handleSubmit}
      errors={errors}
      setErrors={setErrors}
      values={values}
      evaluationItems={evaluationItems}
      handleInputChange={handleInputChange}
      closePopup={props.closePopup}
      question20a={question20a}
      setQuestion20a={setQuestion20a}
      question20b={question20b}
      setQuestion20b={setQuestion20b}
      question20c={question20c}
      setQuestion20c={setQuestion20c}
      toggleQuestion20Input={toggleQuestion20Input}
    />
  );

  if (loading) {
    courseInfoForm = <Spinner />;
    serviceForm = <Spinner />;
  }

  if (evaluationSend) {
    serviceForm = (
      <Alert severity='success'>
        <AlertTitle>Evaluación finalizada</AlertTitle>
      </Alert>
    );
  }

  switch (step) {
    case 1:
      return courseInfoForm;
    case 2:
      return <EvaluationInfoForm nextStep={nextStep} prevStep={prevStep} />;
    case 3:
      return (
        <EventForm
          nextStep={nextStep}
          prevStep={prevStep}
          errors={errors}
          setErrors={setErrors}
          values={values}
          evaluationItems={evaluationItems}
          handleInputChange={handleInputChange}
        />
      );
    case 4:
      return (
        <InstructorForm
          nextStep={nextStep}
          prevStep={prevStep}
          errors={errors}
          setErrors={setErrors}
          values={values}
          evaluationItems={evaluationItems}
          handleInputChange={handleInputChange}
        />
      );
    case 5:
      return (
        <PartakerForm
          nextStep={nextStep}
          prevStep={prevStep}
          errors={errors}
          setErrors={setErrors}
          values={values}
          evaluationItems={evaluationItems}
          handleInputChange={handleInputChange}
        />
      );
    case 6:
      return serviceForm;
    default:
      console.log('Form');
  }
};

export default CourseEvaluationForm;
