import React, { useState, useLayoutEffect } from 'react';
import { Form, useForm } from '../../../shared/useForm';
import Button from '@material-ui/core/Button';
import Input from '../../../components/Controls/Input';
import Spinner from '../../../components/UI/Spinner/Spinner';
import { Alert, AlertTitle } from '@material-ui/lab';
import SnackBar from '../../../components/UI/SnackBar/SnackBar';
import axios from 'axios';

const initialValues = {
  password: '',
};

const UnsubscribeToCourse = (props) => {
  const [loading, setLoading] = useState(false);
  const [snackStatus, setSnackStatus] = useState({
    openSnackBar: false,
    snackBarMessage: '',
    severity: '',
  });
  const {
    auxUnsubscribed,
    setAuxUnsubscribed,
    openUnsubscribe,
    setOpenUnsubscribe,
  } = props;

  useLayoutEffect(() => {
    setAuxUnsubscribed(false);
    // eslint-disable-next-line
  }, [openUnsubscribe]);

  const validate = (fieldValues = values) => {
    let temp = { ...errors };

    if ('password' in fieldValues)
      temp.password = fieldValues.password ? '' : 'Ingrese su contraseña';
    if ('password' in fieldValues)
      temp.password =
        fieldValues.password.length < 6
          ? 'La contraseña debe contener al menos 6 caracteres'
          : '';

    setErrors({
      ...temp,
    });

    if (fieldValues == values) return Object.values(temp).every((x) => x == '');
  };

  const { values, errors, setErrors, handleInputChange } = useForm(
    initialValues,
    true,
    validate
  );
  const { password } = values;

  const handleSubmit = (e) => {
    e.preventDefault();
    //let url = '/auth/unsubscribe-course/'+ props.rowData, {password}
    if (validate()) {
      setLoading((loading) => !loading);

      axios
        .post(
          '/auth/unsubscribe-course/'+ props.rowData,
          { password }
        )
        .then((response) => {
          setLoading((loading) => !loading);
          setAuxUnsubscribed((auxUnsubscribed) => !auxUnsubscribed);
          setTimeout(
            () => setOpenUnsubscribe((openUnsubscribe) => !openUnsubscribe),
            3000
          );
        })
        .catch((error) => {
          setLoading((loading) => !loading);
          if (error.response) {
            //Se realizo la peticion y el servidor respondio con un codigo de status
            setSnackStatus({
              openSnackBar: true,
              severity: 'error',
              snackBarMessage: error.response.data.message,
            });
          } else if (error.request) {
            //Se realizo la peticion pero no se recibio una respuesta
            window.alert('Error: ' + error.request.data.message);
          } else {
            //Algo ocurrio al preparar la peticion que desencadeno un error
            console.log('Error: ' + error.message);
          }
          console.log(error.config);
        });
    }
  };

  let form = (
    <Form onSubmit={handleSubmit}>
      <p>Dar de baja de '{props.rowData}'?</p>
      <Input
        name='password'
        placeholder='Contraseña'
        label='Contraseña'
        type='password'
        value={password}
        onChange={handleInputChange}
        inputProps={{ maxLength: 20 }}
        error={errors.password}
      />

      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
      >
        ACEPTAR
      </Button>
    </Form>
  );

  if (loading) {
    form = <Spinner />;
  }

  if (auxUnsubscribed) {
    form = (
      <Alert severity='success'>
        <AlertTitle>Se ha dado de baja exitosamente</AlertTitle>
      </Alert>
    );
  }

  return (
    <div>
      {form}{' '}
      <SnackBar
        openSnackBar={snackStatus.openSnackBar}
        closeSnackBar={() => setSnackStatus({ openSnackBar: false })}
        snackBarMessage={snackStatus.snackBarMessage}
        severity={snackStatus.severity}
      />
    </div>
  );
};

export default UnsubscribeToCourse;
