import React, { useLayoutEffect, useRef } from 'react';
import { Form } from '../../../shared/useForm';
import Button from '@material-ui/core/Button';
import RadioGroup from '../../../components/Controls/RadioGroup';
import FormHelperText from '@material-ui/core/FormHelperText';
import classes from './EvaluationForm.module.css';

const EventForm = (props) => {
  const divRef = useRef(null);

  useLayoutEffect(() => {
    divRef.current.scrollIntoView({ block: 'end', behavior: 'instant' });
  }, []);

  const {
    values,
    evaluationItems,
    errors,
    setErrors,
    handleInputChange,
  } = props;

  const {
    question1D,
    question1I,
    question2D,
    question2I,
    question3D,
    question3I,
    question4D,
    question4I,
    question5D,
    question5I,
    question6D,
    question6I,
  } = values;

  const validate = () => {
    let temp = { ...errors };

    if (
      question1D &&
      question1I &&
      question2D &&
      question2I &&
      question3D &&
      question3I &&
      question4D &&
      question4I &&
      question5D &&
      question5I &&
      question6D &&
      question6I
    ) {
      temp.formError = '';
    } else {
      temp.formError = 'Asegúrese de contestar todas las preguntas';
    }

    setErrors({
      ...temp,
    });

    if (values) return Object.values(temp).every((x) => x == '');
  };

  const next = (e) => {
    e.preventDefault();
    if (validate()) {
      props.nextStep();
    }
  };

  const prev = (e) => {
    e.preventDefault();
    props.prevStep();
  };

  let form = (
    <Form className={classes.form}>
      <h4 ref={divRef}>EVENTO</h4>
      <div>
        <p>
          1. El desarrollo de los temas que se impartieron estuvieron
          estructurados en forma lógica y apropiada.
        </p>
        <RadioGroup
          name='question1D'
          label='Desempeño'
          value={question1D}
          onChange={handleInputChange}
          items={evaluationItems}
        />

        <RadioGroup
          name='question1I'
          label='Importancia'
          value={question1I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <div>
        <p>2. Los temas respondieron a sus intereses y expectativas.</p>
        <RadioGroup
          name='question2D'
          label='Desempeño'
          value={question2D}
          onChange={handleInputChange}
          items={evaluationItems}
        />

        <RadioGroup
          name='question2I'
          label='Importancia'
          value={question2I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>

      <div>
        <p>
          3. La duración del curso fue la adecuada para asimilar los contenidos.
        </p>
        <RadioGroup
          name='question3D'
          label='Desempeño'
          value={question3D}
          onChange={handleInputChange}
          items={evaluationItems}
        />

        <RadioGroup
          name='question3I'
          label='Importancia'
          value={question3I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <div>
        <p>
          4. El material de apoyo que se le entregó fue claro y sencillo durante
          el desarrollo del curso.
        </p>
        <RadioGroup
          name='question4D'
          label='Desempeño'
          value={question4D}
          onChange={handleInputChange}
          items={evaluationItems}
        />

        <RadioGroup
          name='question4I'
          label='Importancia'
          value={question4I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <div>
        <p>5. La calidad del material de apoyo fue adecuada</p>
        <RadioGroup
          name='question5D'
          label='Desempeño'
          value={question5D}
          onChange={handleInputChange}
          items={evaluationItems}
        />

        <RadioGroup
          name='question5I'
          label='Importancia'
          value={question5I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>

      <div>
        <p>6. El material de apoyo fue suficiente</p>
        <RadioGroup
          name='question6D'
          label='Desempeño'
          value={question6D}
          onChange={handleInputChange}
          items={evaluationItems}
        />

        <RadioGroup
          name='question6I'
          label='Importancia'
          value={question6I}
          onChange={handleInputChange}
          items={evaluationItems}
        />
      </div>
      <FormHelperText error>{errors.formError}</FormHelperText>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={prev}
      >
        Regresar
      </Button>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={next}
      >
        Siguiente
      </Button>
    </Form>
  );

  return <div>{form}</div>;
};

export default EventForm;
