import React, { useLayoutEffect, useRef } from 'react';
import { Form } from '../../../shared/useForm';
import Button from '@material-ui/core/Button';
import classes from './EvaluationForm.module.css';

const EvaluationInfoForm = (props) => {
  const divRef = useRef(null);

  useLayoutEffect(() => {
    divRef.current.scrollIntoView({ block: 'end', behavior: 'instant' });
  }, []);

  const next = (e) => {
    e.preventDefault();
    props.nextStep();
  };

  const prev = (e) => {
    e.preventDefault();
    props.prevStep();
  };

  let form = (
    <Form className={classes.courseInfo}>
      <section>
        <h1 ref={divRef}>Estimado usuario</h1>
        <p>
          La Dirección General de Personal tiene el compromiso de brindar
          servicios con calidad, por tal motivo solicitamos su opinión a través
          de la presente encuesta que nos ayudará a mejorar nuestro servicio y
          desempeño. Agradecemos su atención y nos ponemos a su disposición, La
          Dirección General de Personal.
        </p>
      </section>
      <section>
        <h1>Instrucciones</h1>
        <p>
          La encuesta consta de 16 afirmaciones orientadas al servicio de
          “CAPACITACIÓN”, para lo cual se considera una escala de calificación
          para el Desempeño de la Dirección General de Personal y la Importancia
          que tiene para usted este Servicio, como se presenta a continuación:
        </p>
      </section>
      <hr />
      <section>
        <h1>Escala de calificación</h1>
        <h3>Desempeño</h3>
        <p>
          5: Totalmente de acuerdo. <br />
          4: Parcialmente de acuerdo. <br />
          3: Indiferente. <br />
          2: Parcialmente en desacuerdo <br />
          1: Totalmente en desacuerdo.
        </p>
        <h3>Importancia</h3>
        <p>
          5: Lo mas importante. <br />
          4: importante. <br />
          3: Indiferente. <br />
          2: Poco importante <br />
          1: No importa.
        </p>
      </section>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={prev}
      >
        Regresar
      </Button>
      <Button
        type='submit'
        fullWidth
        variant='contained'
        color='primary'
        style={{ marginTop: '15px' }}
        onClick={next}
      >
        Iniciar encuesta
      </Button>
    </Form>
  );

  return <div>{form}</div>;
};

export default EvaluationInfoForm;
